import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { BlogPost } from 'src/app/models/blogpost';
import { Observable } from 'rxjs';

const perPage = 6;

@Injectable({
  providedIn: 'root',
})
export class PostServiceService {
  constructor(private http: HttpClient) {}

  getPosts(page, tag?, category?): Observable<BlogPost[]> {
    let apiString = `https://clintblogapi.herokuapp.com/api/posts?page=${page}&perPage=${perPage}`;

    if (tag)
      apiString +=
        '&tag=' + (tag.substring(0, 1) == '#' ? tag.substring(1) : tag);

    if (category) apiString += `?category=${category}`;

    return this.http.get<BlogPost[]>(apiString);
  }

  getAllPosts(): Observable<BlogPost[]> {
    let apiString = `https://clintblogapi.herokuapp.com/api/posts?page=1&perPage=${Number.MAX_SAFE_INTEGER}`;

    return this.http.get<BlogPost[]>(apiString);
  }

  newPost(data: BlogPost): Observable<any> {
    return this.http.post<any>(
      `https://clintblogapi.herokuapp.com/api/posts`,
      data
    );
  }

  updatePostById(id: string, data: BlogPost): Observable<any> {
    return this.http.put<any>(
      `https://clintblogapi.herokuapp.com/api/posts/${id}`,
      data
    );
  }

  deletePostById(id: String) {
    return this.http.delete<any>(
      `https://clintblogapi.herokuapp.com/api/posts/${id}`
    );
  }

  getPostbyId(id): Observable<BlogPost> {
    return this.http.get<BlogPost>(
      `https://clintblogapi.herokuapp.com/api/posts/${id}`
    );
  }

  getCategories(): Observable<any> {
    return this.http.get<any>(
      `https://clintblogapi.herokuapp.com/api/categories`
    );
  }

  getTags(): Observable<string[]> {
    return this.http.get<string[]>(
      `https://clintblogapi.herokuapp.com/api/tags`
    );
  }
}
